.game h1 {
    font-size: 2.5em;
  }
  
  .pointsContainer span {
    font-weight: bold;
  }
  
  .tip span {
    color: #ecfa00;
  }
  
  .wordContainer {
    margin: 1.5em;
    padding: 1.5em;
    border: 20px solid #ecfa00;
    display: flex;
  }
  
  .letter,
  .blankSquare {
    font-size: 70px;
    line-height: 1.5;
    border: 3px solid #000;
    height: 100px;
    width: 100px;
    text-transform: uppercase;
    background-color: #fff;
    color: #000;
    font-weight: bold;
  }
  
  .letterContainer p {
    margin-bottom: 1.2em;
  }
  
  .letterContainer form {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .letterContainer input {
    height: 50px;
    width: 50px;
    font-size: 2em;
    text-align: center;
    margin-right: 1em;
  }

  @media only screen and (max-width: 900px){
    .letter,.blankSquare{
      font-size: 20px;
      width: 30px;
      height: 30px;
    }
    .wordContainer {
      margin: 0.5em;
      padding: 0.5em;
      border: 10px solid #ecfa00;
      display: flex;
      justify-content: center;
    }
  }